
.App {
  background: url('./Images/Environments/Takreshspawn.jpg');
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
}
.AppBackgroundOverlay {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(101, 88, 54, 0.68)),to(rgba(49, 45, 19, 0.87)));
  background: -webkit-linear-gradient(top, rgba(101, 88, 54, 0.68) 0%,rgba(49, 45, 19, 0.87) 100%);
  background: -o-linear-gradient(top, rgba(101, 88, 54, 0.68) 0%,rgba(49, 45, 19, 0.87) 100%);
  background: linear-gradient(to bottom, rgba(101, 88, 54, 0.68) 0%,rgba(49, 45, 19, 0.87) 100%);
}

.App .CardContainer {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}



